
import {
  defineComponent,
  onBeforeMount,
  onMounted,
  reactive,
  ref,
  computed,
  UnwrapRef,
} from "vue";

// import FileWorker from "worker-loader!../utils/file.worker";

// import FilterWorker from "worker-loader!../utils/filter.worker";

import FileWorker from "worker-loader!../utils/file.worker";

import FilterWorker from "worker-loader!../utils/filter.worker";
// console.log('FileWorker>>>>>>...',new FileWorker())

interface projectI {
  pageSize: number;
  workerArr: FileWorker[] | FilterWorker[];
  count: number;
  spliceArr: any[];
}
interface filterProjectI extends projectI {
  filter: {};
}

export default defineComponent({
  name: "Home",
  components: {},
  setup() {
    const workerLength = 4;

    const fileLoading = ref(false);

    const loading = ref(false);

    const dataLength = ref(0);

    const filterLength = ref(0);

    const dataSource: Array<any> = reactive([]);

    const columns = reactive([
      {
        title: "Caller",
        dataIndex: "Caller",
        key: "Caller",
      },
      {
        title: "LevelName",
        dataIndex: "LevelName",
        key: "LevelName",
      },
      {
        title: "详细",
        dataIndex: "messageData",
        key: "messageData",
        slots: { customRender: "messageData" },
      },
      {
        title: "时间",
        dataIndex: "TimeString",
        key: "TimeString",
      },
    ]);

    // const tableData: Array<any> = reactive([]);

    const tableData: any[] = [];

    const fileProject: projectI = reactive({
      pageSize: 50,
      workerArr: [],
      count: 0,
      spliceArr: [],
    });

    const filterProject: filterProjectI = reactive({
      pageSize: 50000,
      workerArr: [],
      count: 0,
      spliceArr: [],
      filter: {},
    });

    const formState = reactive({
      name: "",
      region: undefined,
      date1: undefined,
      delivery: false,
      type: [],
      resource: "",
      desc: "",
    });

    const pagination = computed(() => ({
      pageSize: 200,
    }));
    onBeforeMount(() => {
      for (let i = 0; i < workerLength; i++) {
        const worker1 = new FileWorker();

        const worker2 = new FilterWorker();

        worker1.onmessage = getTableRes;

        worker2.onmessage = getFilterRes;

        fileProject.workerArr.push(worker1);

        filterProject.workerArr.push(worker2);
      }
    });

    onMounted(() => {});

    const getTableRes = (e: any) => {
      const data = e.data.data;

      const index = e.data.index;

      console.timeEnd("read-" + index);

      data.forEach((item: any) => {
        tableData.push(item);
      });

      fileProject.count -= fileProject.pageSize;

      if (fileProject.count <= 0) {
        fileLoading.value = false;

        console.timeEnd("file");

        console.log("length", tableData.length);

        console.log("tableData>>>>", tableData);

        dataSource.length = 0;

        tableData.forEach((item: any) => {
          dataSource.push(item);
        });

        dataLength.value = tableData.length;
      } else {
        console.time("read-" + index);

        const postData = fileProject.spliceArr.splice(0, fileProject.pageSize);

        if (postData.length > 0) {
          fileProject.workerArr[index].postMessage({
            data: postData,
            index: index,
          });
        }
      }
    };

    const getFilterRes = (e: any) => {
      const data = e.data.data;

      const index = e.data.index;

      console.timeEnd("filter-" + index);

      data.forEach((item: any) => {
        dataSource.push(item);
      });

      filterProject.count -= filterProject.pageSize;

      if (filterProject.count <= 0) {
        loading.value = false;
        console.log("dataSource.length", dataSource.length);
        console.timeEnd("filter");
      } else {
        console.time("filter-" + index);

        const postData = filterProject.spliceArr.splice(
          0,
          filterProject.pageSize
        );

        if (postData.length > 0) {
          filterProject.workerArr[index].postMessage({
            data: JSON.parse(JSON.stringify(postData)),
            index: index,
            filter: JSON.parse(JSON.stringify(filterProject.filter)),
          });
        }
      }
    };

    const handleChange = (e: Event) => {
      console.log("readFile>>>>>>>>>");

      console.time("file");

      fileLoading.value = true;

      tableData.length = 0;

      const input = e.target as HTMLInputElement;

      const files: FileList = input.files!;

      fileProject.spliceArr = Array.from(files);

      fileProject.count = files.length;

      console.log("fileProject", fileProject);

      for (let i = 0; i < workerLength; i++) {
        console.time("read-" + i);

        const postData = fileProject.spliceArr.splice(0, fileProject.pageSize);

        if (postData.length > 0) {
          console.log("11111>>>>");
          try {
            fileProject.workerArr[i].postMessage({ data: postData, index: i });
          } catch (err) {
            console.log("err>>>>>>>", err);
          }
        }
      }
    };

    const handleSearch = () => {
      console.log("search>>>>>>>>>");
      console.time("filter");
      const filter = { Caller: "SyncDataForItem" };

      loading.value = true;

      dataSource.length = 0;

      filterProject.filter = filter;

      filterProject.spliceArr = tableData;

      filterProject.count = tableData.length;

      for (let i = 0; i < workerLength; i++) {
        console.time("filter-" + i);

        const postData = filterProject.spliceArr.splice(
          0,
          filterProject.pageSize
        );
        if (postData.length > 0) {
          filterProject.workerArr[i].postMessage({
            data: JSON.parse(JSON.stringify(postData)),
            index: i,
            filter: JSON.parse(JSON.stringify(filterProject.filter)),
          });
        }
      }
    };
    return {
      dataSource,
      columns,
      fileLoading,
      loading,
      pagination,
      labelCol: { span: 10 },
      wrapperCol: { span: 10 },
      formState,
      dataLength,
      filterLength,
      handleChange,
      handleSearch,
    };
  },
});
